<template>
  <div
    id="app"
    class="px-5 py-3"
  >
    <div>
      <!-- <div class="mb-3">
        <button
          type="button"
          class="btn btn-secondary me-3"
          @click="indexColumn = !indexColumn"
        >
          switch index column
        </button>

        <button
          type="button"
          class="btn btn-secondary"
          @click="removeColumn"
        >
          remove column
        </button>
      </div> -->
      <div class="mb-3">
        <p>表格数据为静态数据</p>
        <p><strong>Position</strong> 列标题右侧角标为功能菜单入口</p>
      </div>
      <v-grid
        ref="grid"
        :data="gridData"
        :index="indexColumn"
      >
        <el-table-column
          width="150px"
          class-name="v-grid-sortable"
          v-for="col in columns"
          :key="col.prop"
          :prop="col.prop"
          :label="col.name"
        />
        <el-table-column
          prop="position"
          label="Position"
        >
          <template
            slot="header"
            slot-scope="scope"
          >
            <div class="d-flex justify-content-between">
              <div v-text="getHeaderText(scope.column)" />
              <header-menu @condition="dialog = true" />
            </div>
          </template>
        </el-table-column>
      </v-grid>
    </div>

    <el-dialog
      :visible.sync="dialog"
      width="400px"
      title="自定义筛选"
    >
      <div>
        <div class="d-flex">
          <div class="me-3">
            <el-select
              size="small"
              style="width: 120px"
              v-model="conditionType1"
            >
              <el-option
                v-for="item in filterOptions"
                :key="item"
                :label="item"
                :value="item"
              />
            </el-select>
          </div>
          <div class="flex-grow-1">
            <el-input
              size="small"
              v-model="conditionValue1"
            />
          </div>
        </div>
        <div class="my-2">
          与、或
        </div>
        <div class="d-flex mb-3">
          <div class="me-3">
            <el-select
              size="small"
              style="width: 120px"
              v-model="conditionType2"
            >
              <el-option
                v-for="item in filterOptions"
                :key="item"
                :label="item"
                value="item"
              />
            </el-select>
          </div>
          <div class="flex-grow-1">
            <el-input
              size="small"
              v-model="conditionValue2"
            />
          </div>
        </div>
        <div class="d-flex justify-content-end">
          <button
            type="button"
            class="btn btn-primary btn-sm me-3 px-3"
            @click="dialog = false"
          >
            确定
          </button>
          <button
            type="button"
            class="btn btn-light btn-sm px-3"
            @click="dialog = false"
          >
            取消
          </button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  TableColumn,
  Input,
  Dialog,
  Select,
  Option
} from 'element-ui'
import Sortable from 'sortablejs'
import cloneDeep from 'lodash.clonedeep'
import HeaderMenu from './HeaderMenu.vue'
import table from './table'

export default {
  components: {
    'el-table-column': TableColumn,
    'el-input': Input,
    'el-dialog': Dialog,
    'el-select': Select,
    'el-option': Option,
    HeaderMenu
  },
  data () {
    return {
      indexColumn: false,
      dialog: false,
      gridData: {
        grid: {
          total: table.length,
          list: table
        }
      },
      filterOptions: ['等于', '✓ 不等于', '包含', '不包含'],
      conditionType1: '',
      conditionType2: '',
      conditionValue1: '',
      conditionValue2: '',
      columns: [
        { name: 'id', prop: 'id' },
        { name: 'Name', prop: 'name' },
        { name: 'Office', prop: 'office' },
        { name: 'Salary', prop: 'salary' },
        { name: 'Extn', prop: 'extn' },
        { name: 'StartDate', prop: 'start_date' }
      ]
    }
  },
  methods: {
    getHeaderText (column) {
      // console.log(JSON.stringify(column, null, 2))
      return column.label
    },
    setupSortable () {
      const header = this.$refs.grid.$el.querySelector('.el-table__header-wrapper tr')
      Sortable.create(header, {
        animation: 180,
        delay: 0,
        draggable: '.v-grid-sortable',
        dragClass: 'bg-secondary',
        ghostClass: 'text-primary',
        onEnd: e => {
          const { oldIndex, newIndex } = e
          if (oldIndex === newIndex) return

          // console.log('oldIndex', oldIndex)
          // console.log('newIndex', newIndex)
          const { columns } = this

          // 组织交换的列索引及模型
          const sortedSwapColumns = [{
            index: newIndex,
            column: cloneDeep(columns[oldIndex])
          }, {
            index: oldIndex,
            column: cloneDeep(columns[newIndex])
          }].sort((a, b) => {
            return a.index - b.index
          })
          // console.log(sortedSwapColumns)
          // 将需要互换的两个列移除
          this.columns = this.columns.filter((val, index) => {
            return ![newIndex, oldIndex].includes(index)
          })

          this.$nextTick(() => {
            sortedSwapColumns.forEach(val => {
              const { index, column } = val
              this.columns.splice(index, 0, column)
            })
            // console.table(this.columns)
          })
        }
      })
      // console.log(sortable)
    },
    removeColumn () {
      this.columns = this.columns.filter((val, index) => index > 0)
    }
  },
  mounted () {
    this.setupSortable()
  }
}
</script>

<style lang="sass">
.v-grid
  .el-table__header th,
  .el-table__header tr
    // background-color: #f5f5f5 !important
    color: #333
  .el-table
    box-shadow: 0 1px 5px rgba(0, 0, 0, .15)
    border-radius: 5px
</style>
