<template>
  <div>
    <el-dropdown
      trigger="click"
      ref="dropdown"
    >
      <div class="d-inline-flex rounded-3 lh-1 p-1 bg-white shadow-sm">
        <i class="el-icon-caret-bottom" />
      </div>
      <el-dropdown-menu slot="dropdown">
        <div
          class="px-2"
          style="width: 300px;"
        >
          <section class="mb-2">
            <h6>排序</h6>
            <div class="d-flex align-items-center">
              <div class="d-flex flex-grow-1 p-1 bg-light rounded-pill">
                <div
                  class="rounded-pill px-3"
                  :class="getSortClasses('')"
                  @click="changeSortType('')"
                >
                  <i class="el-icon-sort" />
                  默认
                </div>
                <div
                  class="rounded-pill px-3"
                  :class="getSortClasses('asc')"
                  @click="changeSortType('asc')"
                >
                  <i class="el-icon-sort-up" />
                  升序
                </div>
                <div
                  class="rounded-pill px-3"
                  :class="getSortClasses('desc')"
                  @click="changeSortType('desc')"
                >
                  <i class="el-icon-sort-down" />
                  降序
                </div>
              </div>
            </div>
          </section>

          <section class="mb-2">
            <h6>筛选</h6>
            <div class="d-flex">
              <div class="flex-grow-1 me-2">
                <select
                  class="form-select form-select-sm"
                  v-model="filterType"
                >
                  <option
                    v-for="item in filterOptions"
                    :key="item"
                    :value="item"
                    v-text="item"
                  />
                </select>
              </div>
              <div class="btn-group">
                <button
                  type="button"
                  class="btn btn-outline-primary btn-sm"
                  @click="openConditionSetting"
                >
                  设置
                </button>
                <button
                  type="button"
                  class="btn btn-outline-secondary btn-sm"
                  v-if="isSetupFilter"
                  @click="cleanCondition"
                >
                  清除
                </button>
              </div>
            </div>
          </section>

          <section>
            <h6>过滤</h6>
            <div class="mb-2">
              <el-input
                v-model="filterText"
                placeholder="搜索"
                size="mini"
                @input="filterContent"
              />
            </div>
            <div
              class="border rounded-3 p-2 overflow-auto"
              style="height: 150px;"
            >
              <div>
                <el-checkbox
                  :indeterminate="isIndeterminate"
                  v-model="checkAll"
                  @change="handleCheckAllChange"
                >
                  <strong>全选</strong>
                </el-checkbox>
              </div>

              <el-checkbox-group
                v-model="checkedCities"
                @change="handleCheckedCitiesChange"
              >
                <el-checkbox
                  class="d-flex align-items-center"
                  v-for="city in cities"
                  :label="city"
                  :key="city"
                >
                  {{ city }}
                </el-checkbox>
              </el-checkbox-group>
            </div>
          </section>
        </div>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
import {
  Dropdown,
  DropdownMenu,
  Checkbox,
  CheckboxGroup,
  Input
} from 'element-ui'

const cityOptions = ['上海', '北京', '广州', '深圳', '福州', '杭州', '天津', '南京', '武汉', '长沙', '兰州']

export default {
  components: {
    'el-dropdown': Dropdown,
    'el-dropdown-menu': DropdownMenu,
    'el-checkbox': Checkbox,
    'el-checkbox-group': CheckboxGroup,
    'el-input': Input
  },
  data () {
    return {
      checkAll: false,
      checkedCities: ['上海', '北京'],
      cities: cityOptions,
      isIndeterminate: true,
      sortType: '',
      filterText: '',
      filterType: '',
      filterOptions: ['等于', '✓ 不等于', '包含', '不包含'],
      conditionType1: '',
      conditionType2: '',
      conditionValue1: '',
      conditionValue2: ''
    }
  },
  computed: {
    isSetupFilter () {
      return this.filterType
    }
  },
  methods: {
    handleCheckAllChange (val) {
      this.checkedCities = val ? cityOptions : []
      this.isIndeterminate = false
    },
    handleCheckedCitiesChange (value) {
      const checkedCount = value.length
      this.checkAll = checkedCount === this.cities.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length
    },
    filterContent () {
      const text = this.filterText.trim()
      this.cities = cityOptions.filter(city => city.includes(text))
    },
    changeSortType (type) {
      this.sortType = type
      this.hideDropdown()
    },
    getSortClasses (type) {
      return this.sortType === type ? 'bg-white shadow-sm' : ''
    },
    hideDropdown () {
      this.$refs.dropdown.hide()
    },
    openConditionSetting () {
      this.$emit('condition')
      this.hideDropdown()
    },
    cleanCondition () {
      this.filterType = ''
      this.hideDropdown()
    }
  }
}
</script>
